import React,{useState,useEffect} from 'react';
import About from './About';
import CV from './CV';
import Projects from './Projects';
import Skills from './Skills';

const Body = (props)=>{
  return (
    <>
	{(props.page=='about')&&<About/>}
    {(props.page=='cv')&&<CV/>}	
    {(props.page=='projects')&&<Projects/>}	
    {(props.page=='skills')&&<Skills/>}	
    </>
  );
}

export default Body;